
'use client';

import type { ReactNode } from 'react';
import { createContext, useContext, useState, useCallback, useEffect, useRef } from 'react';

interface LoadingContextType {
  isLoading: boolean;
  loadingProgress: number;
  startLoading: () => void;
  stopLoading: () => void;
}

const LoadingContext = createContext<LoadingContextType | undefined>(undefined);

export function LoadingProvider({ children }: { children: ReactNode }) {
  const [isLoading, setIsLoading] = useState(false);
  const [loadingProgress, setLoadingProgress] = useState(0);
  const progressIntervalRef = useRef<NodeJS.Timeout | null>(null);

  const startLoading = useCallback(() => {
    setIsLoading(true);
    setLoadingProgress(0); // Reset progress
    setLoadingProgress(5); // Start with a small progress

    if (progressIntervalRef.current) {
      clearInterval(progressIntervalRef.current);
    }

    progressIntervalRef.current = setInterval(() => {
      setLoadingProgress((prev) => {
        if (prev >= 90) { // Cap progress at 90% during loading
          if (progressIntervalRef.current) {
            clearInterval(progressIntervalRef.current);
            progressIntervalRef.current = null;
          }
          return prev;
        }
        return prev + 5; // Increment progress
      });
    }, 150);
  }, [setIsLoading, setLoadingProgress]); // Stable dependencies

  const stopLoading = useCallback(() => {
    if (progressIntervalRef.current) {
      clearInterval(progressIntervalRef.current);
      progressIntervalRef.current = null;
    }
    
    // Logic to only complete animation if it was actually loading
    // This uses the isLoading state variable. The callback's identity will change when isLoading changes.
    // However, this is usually acceptable if the useEffects consuming this are careful with their deps.
    if (isLoading) {
      setLoadingProgress(100);
      setTimeout(() => {
        setIsLoading(false);
        setLoadingProgress(0); // Reset for next time
      }, 300); // Keep it at 100% for a bit, then hide
    } else {
        // If not loading, but stop is called (e.g. by route change completion), ensure progress is reset.
        if (loadingProgress > 0 && loadingProgress < 100) {
             setLoadingProgress(100); // Quickly finish
             setTimeout(() => {
                setLoadingProgress(0);
             }, 50);
        } else {
            setLoadingProgress(0); // Just reset if it was already 0 or 100
        }
    }
  }, [isLoading, setIsLoading, setLoadingProgress, loadingProgress]); // loadingProgress added to dependencies for the "else" block

  // Cleanup effect for component unmount
  useEffect(() => {
    return () => {
      if (progressIntervalRef.current) {
        clearInterval(progressIntervalRef.current);
      }
    };
  }, []);

  return (
    <LoadingContext.Provider value={{ isLoading, loadingProgress, startLoading, stopLoading }}>
      {children}
    </LoadingContext.Provider>
  );
}

export function useLoading(): LoadingContextType {
  const context = useContext(LoadingContext);
  if (context === undefined) {
    throw new Error('useLoading must be used within a LoadingProvider');
  }
  return context;
}

