
'use client';

import { useEffect } from 'react';
import { usePathname, useSearchParams } from 'next/navigation';
import { useLoading } from '@/contexts/loading-context';
import { Progress } from '@/components/ui/progress';

export default function LoadingProgressBar() {
  const { isLoading, loadingProgress, stopLoading } = useLoading();
  const pathname = usePathname();
  const searchParams = useSearchParams();

  useEffect(() => {
    // This effect runs when the page route changes (pathname or searchParams).
    // We use it to signal that loading should stop, assuming navigation has completed.
    // Check isLoading to prevent stopping if it wasn't explicitly started for this navigation.
    if (isLoading) {
       stopLoading();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname, searchParams]); // stopLoading is stable, no need to include it as a dep if it causes issues

  if (!isLoading && loadingProgress === 0) { // Ensure it's fully hidden when not loading and reset
    return null;
  }

  return (
    <Progress
      value={loadingProgress}
      className="fixed top-0 left-0 w-full h-1 z-[101] bg-transparent [&>div]:bg-primary"
      style={{ borderRadius: 0, transition: loadingProgress === 100 ? 'opacity 0.3s ease-out' : 'none', opacity: isLoading || loadingProgress === 100 ? 1 : 0 }}
      aria-hidden={!isLoading && loadingProgress === 0}
    />
  );
}
