
'use client';

import Link, { type LinkProps } from 'next/link';
import type { ReactNode, MouseEvent } from 'react';
import { useLoading } from '@/contexts/loading-context';

interface LoadingLinkProps extends LinkProps {
  children: ReactNode;
  className?: string;
  onClick?: (event: MouseEvent<HTMLAnchorElement>) => void;
}

export default function LoadingLink({ children, className, onClick, ...props }: LoadingLinkProps) {
  const { startLoading } = useLoading();

  const handleClick = (event: MouseEvent<HTMLAnchorElement>) => {
    // Check if it's a link to the same page with a hash, or a mailto/tel link
    const href = props.href.toString();
    const currentPath = window.location.pathname + window.location.search;

    if (href.startsWith('#') || href.startsWith('mailto:') || href.startsWith('tel:')) {
      // Don't start loading for same-page hash links or mailto/tel
    } else if (href === currentPath || (props.as && props.as.toString() === currentPath)) {
       // Don't start loading if navigating to the exact same page (href or as prop)
    }
    else {
      startLoading();
    }
    
    if (onClick) {
      onClick(event);
    }
  };

  return (
    <Link className={className} {...props} onClick={handleClick}>
      {children}
    </Link>
  );
}
