
'use client';

import type { Metadata } from 'next';
import { Geist, Geist_Mono } from 'next/font/google';
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import { LoadingProvider, useLoading } from '@/contexts/loading-context';
import LoadingProgressBar from '@/components/loading-progress-bar';
// Header component is no longer imported directly here for global use
import FooterComponent from '@/components/footer';
import {
  SidebarProvider,
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarInset,
  SidebarTrigger,
  SidebarFooter,
} from '@/components/ui/sidebar';
import LoadingLink from '@/components/loading-link';
import { LayoutDashboard, PlusCircle, LogOut, Home, LogIn, Loader2, ListFilter, Settings } from 'lucide-react'; // Added Settings for potential future use
import { getSessionUser } from '@/lib/actions';
import { useEffect, useState } from 'react';
import type { User as UserType } from '@/lib/types';
import { Button } from '@/components/ui/button';
import UserAvatar from '@/components/user-avatar';
import { useRouter, usePathname } from 'next/navigation';
import { Badge } from '@/components/ui/badge';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";


const geistSans = Geist({
  variable: '--font-geist-sans',
  subsets: ['latin'],
});

const geistMono = Geist_Mono({
  variable: '--font-geist-mono',
  subsets: ['latin'],
});

// AppShell contains the visual structure and uses hooks safely within LoadingProvider
function AppShell({ children }: { children: React.ReactNode }) {
  const [currentUser, setCurrentUser] = useState<UserType | null>(null);
  const [isLoadingUser, setIsLoadingUser] = useState(true);
  const { startLoading } = useLoading();
  const router = useRouter();
  const pathname = usePathname();

  useEffect(() => {
    async function fetchUser() {
      setIsLoadingUser(true);
      try {
        const user = await getSessionUser();
        setCurrentUser(user);
      } catch (error) {
        console.error("Failed to fetch session user for AppShell:", error);
        setCurrentUser(null);
      } finally {
        setIsLoadingUser(false);
      }
    }
    fetchUser();
  }, [pathname]);

  const handleLogout = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    startLoading();
    event.currentTarget.submit();
  };

  const handleLoginRedirect = () => {
    startLoading();
    router.push('/login');
  };

  // This hidden form is for the sidebar logout button
  const GlobalLogoutForm = () => (
    <form id="global-logout-form" action="/api/auth/logout" method="POST" onSubmit={handleLogout} className="hidden"></form>
  );

  return (
    <>
      <LoadingProgressBar />
      <GlobalLogoutForm />
      {/* Header component removed from here */}

      <div className="flex flex-1 min-h-screen"> {/* Ensure flex container takes full height */}
        <SidebarProvider defaultOpen>
          <Sidebar
            collapsible="icon"
            variant="sidebar"
            className="border-r bg-background text-foreground group hidden md:flex md:flex-col h-screen sticky top-0 z-40" // Changed: h-screen, top-0
            style={{ '--sidebar-width': '260px' } as React.CSSProperties}
          >
            <SidebarHeader className="p-4 flex items-center gap-3 border-b">
              {/* LOGO consolidated here */}
              <LoadingLink href="/" className="flex items-center space-x-2">
                <span className="font-bold sm:inline-block text-lg group-data-[collapsible=icon]:hidden">
                  MineVent
                </span>
                 {/* Visible Gem Icon only when sidebar is collapsed */}
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" className="h-6 w-6 text-primary hidden group-data-[collapsible=icon]:block">
                    <rect width="256" height="256" fill="none"></rect>
                    <path d="M128,24a104.1,104.1,0,0,0-96,128.4L64.3,213.8a8.1,8.1,0,0,0,11.4,11.4l62.3-32.3,62.3,32.3a8.1,8.1,0,0,0,11.4-11.4L192,152.4A104.1,104.1,0,0,0,128,24Zm0,192.1L101.8,203,128,189.9l26.2,13.1Z" fill="currentColor" className="text-primary"></path>
                    <path d="M200,144.5,128,184,56,144.5V112L128,32l72,80Z" opacity="0.2" fill="currentColor" className="text-primary"></path>
                </svg>
              </LoadingLink>
              <Badge variant="outline" className="border-orange-500 text-orange-600 dark:border-orange-400 dark:text-orange-400 text-xs px-1.5 py-0.5 group-data-[collapsible=icon]:hidden">Beta</Badge>
            </SidebarHeader>

            <SidebarContent className="p-2 flex-grow">
              <SidebarMenu>
                <SidebarMenuItem>
                  <SidebarMenuButton asChild isActive={pathname === '/'}>
                    <LoadingLink href="/">
                      <Home />
                      <span className="group-data-[collapsible=icon]:hidden">首页</span>
                    </LoadingLink>
                  </SidebarMenuButton>
                </SidebarMenuItem>
                {!isLoadingUser && currentUser && (
                  <SidebarMenuItem>
                    <SidebarMenuButton asChild isActive={pathname.startsWith('/dashboard')}>
                      <LoadingLink href="/dashboard">
                        <LayoutDashboard />
                        <span className="group-data-[collapsible=icon]:hidden">仪表盘</span>
                      </LoadingLink>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                )}
                {!isLoadingUser && currentUser?.op === 1 && (
                  <>
                    <SidebarMenuItem>
                      <SidebarMenuButton asChild isActive={pathname.startsWith('/admin/create-event')}>
                        <LoadingLink href="/admin/create-event">
                          <PlusCircle />
                          <span className="group-data-[collapsible=icon]:hidden">创建活动</span>
                        </LoadingLink>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                    <SidebarMenuItem>
                      <SidebarMenuButton asChild isActive={pathname.startsWith('/admin/manage-events')}>
                        <LoadingLink href="/admin/manage-events">
                          <ListFilter />
                          <span className="group-data-[collapsible=icon]:hidden">管理活动</span>
                        </LoadingLink>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                  </>
                )}
              </SidebarMenu>
            </SidebarContent>

            <SidebarFooter className="p-3 border-t mt-auto">
              {!isLoadingUser && currentUser ? (
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                     <Button variant="ghost" className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:px-0">
                        <UserAvatar user={currentUser} className="h-9 w-9 group-data-[collapsible=icon]:mr-0 mr-2" />
                        <div className="group-data-[collapsible=icon]:hidden text-left">
                          <p className="text-sm font-medium truncate">{currentUser.username}</p>
                          <p className="text-xs text-muted-foreground">{currentUser.op === 1 ? '管理员' : '用户'}</p>
                        </div>
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent className="w-56 mb-2 group-data-[collapsible=icon]:ml-12" align="end" side="top" forceMount>
                    <DropdownMenuLabel className="font-normal">
                      <div className="flex flex-col space-y-1">
                        <p className="text-sm font-medium leading-none">{currentUser.username}</p>
                        <p className="text-xs leading-none text-muted-foreground">
                          {currentUser.id}
                        </p>
                      </div>
                    </DropdownMenuLabel>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem asChild>
                      <LoadingLink href="/dashboard">
                        <LayoutDashboard className="mr-2 h-4 w-4" />
                        <span>我的仪表盘</span>
                      </LoadingLink>
                    </DropdownMenuItem>
                    {currentUser.op === 1 && (
                      <>
                        <DropdownMenuItem asChild>
                          <LoadingLink href="/admin/create-event">
                            <PlusCircle className="mr-2 h-4 w-4" />
                            <span>创建活动</span>
                          </LoadingLink>
                        </DropdownMenuItem>
                        <DropdownMenuItem asChild>
                          <LoadingLink href="/admin/manage-events">
                            <ListFilter className="mr-2 h-4 w-4" />
                            <span>管理活动</span>
                          </LoadingLink>
                        </DropdownMenuItem>
                      </>
                    )}
                    <DropdownMenuSeparator />
                    <DropdownMenuItem asChild>
                         <Button type="submit" form="global-logout-form" variant="ghost" className="w-full justify-start p-2 h-auto text-sm relative cursor-default select-none items-center gap-2 outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                            <LogOut className="mr-2 h-4 w-4" />
                            <span>退出登录</span>
                          </Button>
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              ) : !isLoadingUser && !currentUser ? (
                <Button variant="default" size="sm" onClick={handleLoginRedirect} className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:px-0">
                  <LogIn className="h-4 w-4 group-data-[collapsible=icon]:mr-0 mr-2" />
                  <span className="group-data-[collapsible=icon]:hidden">登录/注册</span>
                </Button>
              ) : (
                <div className="flex items-center justify-center h-10">
                  <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
                </div>
              )}
            </SidebarFooter>
          </Sidebar>

          <SidebarInset className="flex-1 flex flex-col overflow-y-auto">
             {/* Mobile Sidebar Trigger: sticky top-0 to align with where header was */}
            <div className="md:hidden p-4 border-b bg-background sticky top-0 z-30"> {/* Ensure z-index is high enough */}
              <SidebarTrigger />
            </div>
            <main className="flex-grow w-full p-4 md:p-6"> {/* Standard padding for main content */}
              {children}
            </main>
            <FooterComponent />
          </SidebarInset>
        </SidebarProvider>
      </div>
      <Toaster />
    </>
  );
}

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="zh-CN" suppressHydrationWarning>
      <head>
        {/* Metadata can be defined here or in page.tsx files */}
        {/* Example: <title>MineVent</title> */}
      </head>
      <body
        className={`${geistSans.variable} ${geistMono.variable} font-sans antialiased flex flex-col min-h-screen bg-muted/30`}
      >
        <LoadingProvider>
          <AppShell>{children}</AppShell>
        </LoadingProvider>
      </body>
    </html>
  );
}
